import { Component, OnInit } from '@angular/core';
import { Event, Router, RouterEvent, NavigationStart, NavigationEnd } from '@angular/router';


import { Contrato, Seccion } from '../../model.component';
import { ContratosService } from './contratos.service';
import { debounceTime } from 'rxjs/operators';
import { SideNavService } from '../sidenav/sidenav.service';
import { CatalogoService } from '../../catalogo/catalogo.service';
import { isNull } from 'util';

@Component({
  selector: 'app-contratos',
  templateUrl: './contratos.component.html',
  styleUrls: ['./contratos.component.css']
})
export class ContratosComponent implements OnInit {

  contratos: Contrato[];
  categorias: Seccion[];
  contratoId: string;
  isAdmContrato: true;
  valor_filtro = '';
  showLoadingIndicator = true;

  constructor(private catalogoService: CatalogoService, private contratosService: ContratosService, private router: Router, private sidenavService: SideNavService) {

    this.router.events.subscribe((routerEvent: Event) => {

      if (RouterEvent instanceof NavigationStart) {
        this.showLoadingIndicator = true;
      }
      if (RouterEvent instanceof NavigationEnd) {
        this.showLoadingIndicator = false;
      }      
    });
  }

  ngOnInit() {
    this.catalogoService.getSeccion().subscribe((data: any) => this.categorias = data);    
    this.sidenavService.setIsDisabled(false);
    this.contratosService.setContratoId('0');
    this.contratosService.getContratos().subscribe((data: any) => this.contratos = data);
  }
  
  mostrarDetalle(contrato: Contrato) {

    if (contrato.qn_tipoContrato==0) {
      this.contratosService.setContratoIncompletoId(contrato.id);
      console.log(contrato.id);
      this.router.navigate(["/registrar-contrato"]);
    }
    else {
      this.sidenavService.setIsDisabled(true);
      this.contratosService.setContratoId(contrato.id.toString());
      this.router.navigate(["/DatosGenerales"]);
    }



    

    
    
  }


}





